@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Balsamiq+Sans:ital@1&display=swap');

*{
  font-size: 18px;
  font-family: 'Lobster', cursive;
}

.caculator{
  background-color: black;
  height: 450px;
  border-radius: 15px;
}

.keypad{
  font-weight: bold;
  font-size: 25px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(70px, auto);
}

input{
  height: 80px;
  width: 100%;
  background-color: blanchedalmond;
  color: black;
  text-align: right;
  font-size: 35px;
  font-weight: 500;
  letter-spacing: 1px;
  border-radius: 5px;
}

#clear{
  grid-column: 1/4;
  color: brown;
  font-family: 'Balsamiq Sans', cursive;
  font-size: 30px;
}

button{
  text-align: center;
  margin: 7px;
  border-radius: 25px;
  font-weight: bold;
  font-size: 25px;
}

button:hover{
  background-color: goldenrod;
}

.btn{
  background-color: skyblue;
  font-weight: bold;
  font-size: 25px;
}


